button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5rem;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1rem;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  color: darkred;
}

/* Nav */
.navbar {
  display: flex;
  align-items: center;
  padding: 0 18px;
  background-color: bisque;
}

.navbar-logo {
  font-size: 28px;
  color: darkseagreen;
}

.navbar-title {
  padding: 0 18px;
}

.navbar-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  background-color: forestgreen;
  color: white;
  border-radius: 50%;
}

/* HabitAdd */
.add-form {
  padding: 0 18px;
  margin-top: 16px;
}

.add-input {
  font-size: large;
  margin-right: 8px;
}

.add-button {
  background-color: forestgreen;
  font-size: large;
  padding: 4px 8px;
  color: aliceblue;
}

footer {
  padding: 0 18px;
}

.habit-reset {
  background-color: forestgreen;
  color: aliceblue;
  font-size: large;
  padding: 4px 8px;
}
